.filter__toggle {
  font: 400 11px "Lora", serif;
  color: #a4a4a4;
  float: right;
  padding-right: 35px;
  margin-top: 4px;
  position: relative;
}

.filter__toggle::after {
  content: '';
  position: absolute;
  background: url(../img/category-toggle.png) no-repeat;
  width: 14px;
  height: 9px;
  top: 50%;
  margin-top: -4px;
  right: 12px;
}

.filter__checkboxes {
  padding-top: 25px;
  border-top: 1px solid #fff;
  padding-bottom: 25px;
}

.filter__checkboxes.hidden {
  display: none;
}

.filter__checkbox {
  font: 400 15px "Lora", serif;
  cursor: pointer;
  display: block;
  margin-bottom: 14px;
  padding-left: 30px;
  position: relative;
  color: #808080;
}

.filter__checkbox.active {
  color: #000;
}

.filter__checkbox.active + .sub-filter {
  display: block;
}

.filter__checkbox input {
  display: none;
}

.filter__checkbox input:checked + .filter__checkbox--ico {
  background-image: url(../img/sprite.png);
  background-position: 0px 0px;
  width: 14px;
  height: 14px;
}

.filter__checkbox--ico {
  position: absolute;
  left: 0;
  top: 2px;
  background-image: url(../img/sprite.png);
  background-position: -18px 0px;
  width: 14px;
  height: 14px;
}

.sort__wrapper {
  border-top: 1px solid #fff;
  padding: 20px;
  border-bottom: 1px solid #d7d7d7;
}

.item--select {
  display: inline-block;
  vertical-align: middle;
  margin: 0 0 0 10px;
  width: 150px;
}

.catalog__row .product__item {
  margin-bottom: 30px;
}

.pagination__wrapper {
  padding: 9px 20px;
}

.pagination__wrapper a,
.pagination__wrapper .sep {
  font: 400 16px "Lora", serif;
  color: #a0a0a0;
  text-decoration: none;
}

.item__cart {
  padding-top: 40px;
  border-top: 1px solid #fff;
  margin-bottom: 40px;
}

.item__cart-inner {
  background: #fff;
  padding: 35px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
}

.item__cart-col {
  width: 50%;
}

.item__cart-col.img-col {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}

.item__cart-col.img-col img {
  margin: auto;
}

.item__cart-sku {
  padding-bottom: 15px;
  border-bottom: 1px solid #cccccc;
  font: 400 12px "Lora", serif;
  color: #4c4c4c;
}

.item__cart-price {
  padding: 30px 0;
  font: 400 38px "DendaNew", serif;
  color: #363430;
  background: url(../img/item-border.png) bottom left repeat-x;
}

.item__cart-price span {
  font-size: 28px;
}

.item__cart-price a {
  font: 400 14px "Lora", serif;
  color: #89b3c6;
  margin-left: 20px;
  vertical-align: middle;
  display: inline-block;
}

.item__cart-title {
  font: 400 28px "DendaNew", serif;
  color: #363430;
}

.item__cart-size,
.item__cart-description {
  margin: 0;
  font: 400 14px/22px "Lora", serif;
  color: #4c4c4c;
}

.item__cart-size strong,
.item__cart-description strong {
  color: #000000;
  margin-right: 10px;
}

.item__cart-buy {
  margin-top: 60px;
}

.item__cart-buy .popup__input {
  display: inline-block;
  vertical-align: middle;
  width: 175px;
  margin-bottom: 0;
}

.item__cart-submit {
  width: 193px;
  height: 45px;
  background-color: #b9aca6;
  background-image: linear-gradient(180deg, #b9aca6 0%, #a79a94 100%);
  border: 1px solid #b9aca6;
  border-radius: 7px;
  box-shadow: inset 0 3px 0 #d4c7c1;
  padding: 0;
  cursor: pointer;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
  font: 400 14px/46px "DendaNew", serif;
  color: #3e342f;
  text-transform: uppercase;
  margin-left: 20px;
}

.item__cart-submit:hover {
  background-image: linear-gradient(180deg, #a79a94 0%, #b9aca6 100%);
  box-shadow: inset 0 -3px 0 #d4c7c1;
}

.cart__page {
  margin-top: 25px;
}

.cart__page-top {
  padding: 30px 0 28px;
  border-top: 1px solid #d7d7d7;
  border-bottom: 1px solid #d7d7d7;
  margin-bottom: 4px;
}

.cart__page-title {
  float: left;
  font: 400 28px "DendaNew", serif;
  color: #363430;
  float: left;
  margin: 0;
  padding-left: 10px;
}

.cart__page-clean {
  float: right;
  font: 400 12px "Lora", serif;
  color: #ed1c24;
  text-decoration: none;
  margin-right: 20px;
  padding-right: 35px;
  margin-top: 8px;
  background: url(../img/del.png) right center no-repeat;
}

.cart__table {
  width: 100%;
  table-layout: fixed;
  border-collapse: collapse;
}

.cart__table td {
  border-top: 1px solid #d7d7d7;
  padding: 30px 15px 15px;
  vertical-align: top;
}

.cart__table tr:last-child td {
  border-bottom: 1px solid #d7d7d7;
}

.cart__table-img {
  background: #f8f8f8;
  width: 130px;
  padding-top: 15px !important;
}

.cart__table-img img {
  max-width: 100%;
  height: auto;
  display: block;
  margin: auto;
}

.cart__table-header {
  width: 200px;
}

.cart__table-title {
  font: 700 16px "Lora", serif;
  color: #000;
  margin: 0 0 5px;
}

.cart__table-dimensions {
  color: #4c4c4c;
  font: 400 16px "Lora", serif;
  margin: 0;
}

.cart__table-sku {
  width: 115px;
  font: 400 14px "Lora", serif;
  color: #4c4c4c;
}

.cart__table-price {
  font: 400 16px "DendaNew", serif;
  color: #363430;
  width: 130px;
}

.cart__table-count {
  width: 130px;
  padding-top: 20px !important;
}

.cart__table-count input {
  outline: 0;
  display: inline-block;
  vertical-align: middle;
  width: 50px;
  height: 35px;
  background-color: white;
  border: 1px solid #e1e1e1;
  border-radius: 7px;
  text-align: center;
}

.cart__table-total {
  width: 130px;
  font: 400 20px "DendaNew", serif;
  color: #010101;
}

.cart__table-delete {
  padding-left: 2px !important;
}

.cart__table-delete a {
  background: url(../img/del.png);
  width: 13px;
  height: 13px;
  display: block;
  margin-top: 3px;
}

.cart__totals {
  margin-top: 50px;
  text-align: right;
}

.cart__totals-price {
  font: 400 20px "DendaNew", serif;
  color: #010101;
}

.cart__totals-price span {
  font-weight: 700;
  margin: 0 5px;
}

.cart__totals-done {
  display: inline-block;
  width: 231px;
  height: 45px;
  background-color: #b9aca6;
  background-image: linear-gradient(180deg, #b9aca6 0%, #a79a94 100%);
  border: 1px solid #b9aca6;
  border-radius: 7px;
  box-shadow: inset 0 3px 0 #d4c7c1;
  text-align: center;
  font: 400 14px/46px "DendaNew", serif;
  color: #3e342f;
  text-decoration: none;
  text-transform: uppercase;
}

.cart__totals-done:hover {
  background-image: linear-gradient(180deg, #a79a94 0%, #b9aca6 100%);
  box-shadow: inset 0 -3px 0 #d4c7c1;
}

.cart__page {
  padding-bottom: 40px;
}

@media all and (max-width: 900px) {
  .mfp-arrow {
    -webkit-transform: scale(0.75);
    -ms-transform: scale(0.75);
    transform: scale(0.75);
  }

  .mfp-arrow-left {
    -webkit-transform-origin: 0;
    -ms-transform-origin: 0;
    transform-origin: 0;
  }

  .mfp-arrow-right {
    -webkit-transform-origin: 100%;
    -ms-transform-origin: 100%;
    transform-origin: 100%;
  }

  .mfp-container {
    padding-left: 6px;
    padding-right: 6px;
  }
}

@media screen and (max-width: 800px) and (orientation: landscape), screen and (max-height: 300px) {
  /**
       * Remove all paddings around the image on small screen
       */

  .mfp-img-mobile .mfp-image-holder {
    padding-left: 0;
    padding-right: 0;
  }

  .mfp-img-mobile img.mfp-img {
    padding: 0;
  }

  .mfp-img-mobile .mfp-figure:after {
    top: 0;
    bottom: 0;
  }

  .mfp-img-mobile .mfp-figure small {
    display: inline;
    margin-left: 5px;
  }

  .mfp-img-mobile .mfp-bottom-bar {
    background: rgba(0, 0, 0, 0.6);
    bottom: 0;
    margin: 0;
    top: auto;
    padding: 3px 5px;
    position: fixed;
    box-sizing: border-box;
  }

  .mfp-img-mobile .mfp-bottom-bar:empty {
    padding: 0;
  }

  .mfp-img-mobile .mfp-counter {
    right: 5px;
    top: 3px;
  }

  .mfp-img-mobile .mfp-close {
    top: 0;
    right: 0;
    width: 35px;
    height: 35px;
    line-height: 35px;
    background: rgba(0, 0, 0, 0.6);
    position: fixed;
    text-align: center;
    padding: 0;
  }
}